.btn-header {
  background-color: var(--btn-accent-bg);
  color: var(--hero-text-color);
  transition: background-color var(--time) var(--cubic), color var(--time) var(--cubic), fill var(--time) var(--cubic);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  align-items: center;
  gap: 4px;
  padding: 12px 36px;
  font-weight: 600;
  line-height: 1.2;
  display: flex;
}

.btn-header:focus {
  background-color: var(--btn-hover-color);
  color: var(--btn-text-color);
  fill: var(--header-color-white);
}

.btn-header:hover {
  background-color: var(--btn-hover-color);
}

.btn-hero {
  color: var(--hero-text-color);
  border: 2px solid var(--btn-accent-bg);
  transition: border var(--time) var(--cubic);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 11px 26px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
}

.btn-hero:hover, .btn-hero:focus {
  border: 2px solid var(--btn-hover-color);
}

.btn-hero-secondary {
  color: var(--btn-accent-bg);
  transition: border var(--time) var(--cubic);
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.btn-hero-secondary:hover {
  color: var(--btn-hover-color);
}

.btn-hero-secondary:focus {
  color: var(--btn-text-color);
}

.btn-about {
  background-color: var(--btn-accent-bg);
  color: var(--hero-text-color);
  transition: background-color var(--time) var(--cubic), color var(--time) var(--cubic), fill var(--time) var(--cubic);
